import React, { PureComponent } from "react";
import { connect } from "react-redux";

import { setActiveCity, deleteCity } from "../../../actions/actions";
import store from "../../../store/configureStore";
import AddItem from "./add-item/AddItem";
import CityItem from "./city-item/CityItem";

import "./CityList.scss";

class CityList extends PureComponent {
  constructor(props) {
    super(props);

    this.selectCity = this.selectCity.bind(this);
  }

  selectCity(id) {
    return () => store.dispatch(setActiveCity(id));
  }

  deleteCity(id) {
    return () => store.dispatch(deleteCity(id));
  }

  render() {
    const { cityList, selectedCity } = this.props.state.weatherReducer;

    let cities = [];
    for (let city of cityList.entries()) {
      cities.push(
        <li
          key={city[0]}
          className="city-list-item"
          onClick={this.selectCity(city[0])}
        >
          <CityItem
            data={city[1]}
            isActive={selectedCity === city[0]}
            onDelete={this.deleteCity(city[0])}
          />
        </li>
      );
    }

    return (
      <div className="city-list-wrapper">
        <ul className="city-list">{cities}</ul>
        <AddItem />
      </div>
    );
  }
}

export default connect(state => {
  return { state };
})(CityList);
