import { axiosInstance } from "./api";
import store from "../store/configureStore";
import * as actions from "../actions/actions";

export function getCityWeather(city = "") {
  const objParams = {
    params: { ...axiosInstance.defaults.params }
  };

  if (typeof city === "string") {
    objParams.params.q = city;
  } else {
    objParams.params = { ...city, ...objParams.params };
  }

  const result = axiosInstance.get("/data/2.5/weather", objParams);

  store.dispatch(actions.setLoading(true));

  result
    .then(response => {
      if (response.status === 200) {
        store.dispatch(actions.fetchCity(response.data));
        store.dispatch(actions.setActiveCity(response.data.id));
      }

      store.dispatch(actions.setLoading(false));
    })
    .catch(error => {
      store.dispatch(actions.setError("Something wrong! Try again."));
      store.dispatch(actions.setLoading(false));
    });

  return result;
}
