import React, { PureComponent } from "react";
import { connect } from "react-redux";

import "./Weather.scss";

class Weather extends PureComponent {
  render() {
    let content;
    const { selectedCity, isLoading, error, cityList } = this.props.state.weatherReducer;

    if (isLoading) {
      content = <p>Loading</p>;

    } else if (error !== null) {
      content = <h2>{error}</h2>;

    } else if (cityList.size !== 0) {
      const city = cityList.get(selectedCity);
      const sourceImg = `http://openweathermap.org/img/w/${
        city.weather[0].icon
        }.png`;

      content = (
        <div>
          <img className="img" src={sourceImg} alt="weather-icon" />
          <h3 className="city">{city.name}</h3>
          <h1 className="temp">{city.main.temp.toFixed(1) + "Â°"}</h1>
          <p className="description">{city.weather[0].main}</p>
          <p className="sub-description">{city.weather[0].description}</p>
        </div>
      );
    } else {
      content = <h2>Empty</h2>
    }

    return <div className="weather-container">{content}</div>;
  }
}

export default connect(state => {
  return { state };
})(Weather);
