import React, { PureComponent } from "react";
import classNames from "classnames";

import { keyCodes } from "../../../../constants/consts";
import { getCityWeather } from "../../../../api/apiRequest";

import "./AddItem.scss";

export default class AddItem extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isShowInput: false,
      isError: false,
      isLoading: false
    };

    this.showInput = this.showInput.bind(this);
    this.keyPress = this.keyPress.bind(this);
  }

  showInput(isShow) {
    return () => this.setState({ isShowInput: isShow });
  }

  keyPress(event) {
    const value = event.target.value;

    if (value === "") {
      return;
    }

    if (event.charCode === keyCodes.KEY_ENTER) {
      this.setState({ isLoading: true });

      getCityWeather(value)
        .then(result => {
          this.setState({
            isShowInput: false,
            isError: false,
            isLoading: false
          });
        })
        .catch(error => {
          this.setState({
            isError: true,
            isLoading: false
          });
        });
    }
  }

  render() {
    let content;
    const { isShowInput, isError, isLoading } = this.state;

    if (isShowInput) {
      content = (
        <input
          type="text"
          className={classNames("add-city-input", {
            "add-city-input-error": isError
          })}
          autoFocus={true}
          disabled={isLoading}
          placeholder=" Search..."
          onKeyPress={this.keyPress}
          onBlur={this.showInput(false)}
        />
      );
    } else {
      content = (
        <button className="btn-add-city" onClick={this.showInput(true)}>
          + Add city
        </button>
      );
    }

    return <div className="add-city-container">{content}</div>;
  }
}
