import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import "./CityItem.scss";

export default class CityItem extends PureComponent {
  constructor(props) {
    super(props);

    this.onClickDelete = this.onClickDelete.bind(this);
  }

  onClickDelete(event) {
    const { onDelete } = this.props;

    event.stopPropagation();

    onDelete();
  }

  render() {
    const { data, isActive } = this.props;

    return (
      <div className={classNames("city-item", { "city-item-active": isActive })}>
        <p className="city-name">{data.name}</p>
        <p className="close" onClick={this.onClickDelete}>
          &#10006;
        </p>
      </div>
    );
  }
}

CityItem.propTypes = {
  data: PropTypes.object.isRequired,
  onDelete: PropTypes.func.isRequired,
  isActive: PropTypes.bool.isRequired
};

CityItem.defaultProps = {
  data: null,
  onDelete: () => { },
  isActive: false
};
