import { OrderedMap } from "immutable";

import * as actions from "../actions/actionTypes";
import { putToLocalStorage } from '../utils/helpers';

const initialState = {
  cityList: new OrderedMap(),
  selectedCity: null,
  isLoading: false,
  error: null
};

export default function weatherReducer(state = initialState, action) {
  switch (action.type) {
    case actions.FETCH_CITY:
      const { city } = action;

      //simple put
      putToLocalStorage(city.id, city);

      return {
        ...state,
        error: null,
        cityList: state.cityList.set(city.id, city)
      };

    case actions.SET_LOADING:
      return {
        ...state,
        isLoading: action.isLoading
      };

    case actions.SET_ERROR:
      return {
        ...state,
        error: action.error
      };

    case actions.SET_ACTIVE_CITY:
      return {
        ...state,
        error: null,
        selectedCity: action.id
      };

    case actions.DELETE_CITY:
      const { id } = action;

      let cityList = state.cityList.delete(id);
      let selectedCity =
        state.selectedCity === id
          ? state.cityList.first().id
          : state.selectedCity;

      return {
        ...state,
        cityList,
        selectedCity
      };

    default:
      return state;
  }
}
